export const validationRules = {
  required: (value) => {
    if (!value || value.toString().trim() === '') {
      return 'Este campo es obligatorio'
    }
    return null
  },

  minLength: (min) => (value) => {
    if (value && value.length < min) {
      return `M√≠nimo ${min} caracteres`
    }
    return null
  },

  maxLength: (max) => (value) => {
    if (value && value.length > max) {
      return `M√°ximo ${max} caracteres`
    }
    return null
  },

  email: (value) => {
    if (value && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
      return 'Formato de email inv√°lido'
    }
    return null
  },

  phone: (value) => {
    if (value && !/^\d{9,15}$/.test(value.replace(/\s/g, ''))) {
      return 'Formato de tel√©fono inv√°lido (9-15 d√≠gitos)'
    }
    return null
  },

  alphanumeric: (value) => {
    if (value && !/^[a-zA-Z0-9_]+$/.test(value)) {
      return 'Solo se permiten letras, n√∫meros y guiones bajos'
    }
    return null
  }
}

export const validateField = (value, rules = []) => {
  for (const rule of rules) {
    const error = rule(value)
    if (error) {
      return error
    }
  }
  return null
}

export const validateForm = (formData, validationSchema) => {
  const errors = {}
  let isValid = true

  Object.keys(validationSchema).forEach(field => {
    const fieldRules = validationSchema[field]
    const error = validateField(formData[field], fieldRules)
    if (error) {
      errors[field] = error
      isValid = false
    }
  })

  return { isValid, errors }
}

export const loginValidationSchema = {
  username: [
    validationRules.required,
    validationRules.minLength(3),
    validationRules.maxLength(20),
    validationRules.alphanumeric
  ],
  password: [
    validationRules.required,
    validationRules.minLength(6),
    validationRules.maxLength(50)
  ]
}

export const userValidationSchema = {
  usuario: [
    validationRules.required,
    validationRules.minLength(3),
    validationRules.maxLength(20),
    validationRules.alphanumeric
  ],
  nombre: [
    validationRules.required,
    validationRules.maxLength(50)
  ],
  apellido: [
    validationRules.required,
    validationRules.maxLength(50)
  ],
  email: [
    validationRules.email
  ],
  telefono: [
    validationRules.phone
  ],
  ciudad: [
    validationRules.maxLength(50)
  ],
  direccion: [
    validationRules.maxLength(100)
  ],
  especialidad: [
    validationRules.maxLength(50)
  ]
}