import { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { useAuth } from '../context/AuthContext'
import { useForm } from '../hooks/useForm'
import { loginValidationSchema } from '../utils/validation'
import { logger } from '../utils/logger'
import apiService from '../services/apiService'
import '../styles/Login.css'

const Login = () => {
  const navigate = useNavigate()
  const { login, isAuthenticated, error, clearError, setAuthToken } = useAuth()

  const initialValues = {
    username: '',
    password: ''
  }

  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleChange,
    handleBlur,
    handleSubmit,
    setFieldError
  } = useForm(initialValues, loginValidationSchema)

  useEffect(() => {
    if (isAuthenticated) {
      navigate('/panel', { replace: true })
    }
  }, [isAuthenticated, navigate])

  useEffect(() => {
    clearError()
  }, [])

  const onSubmit = async (formValues) => {
    try {
      clearError()
      const response = await apiService.login(formValues)

      if (response.data) {
        if (response.token) {
          setAuthToken(response.token)
          sessionStorage.setItem('auth_token', response.token)
        }

        sessionStorage.setItem('user_data', JSON.stringify(response.data))
        await login(response.data)
      } else {
        throw new Error('Respuesta inv√°lida del servidor')
      }
    } catch (err) {
      logger.error('Login error:', err)
      setFieldError('general', err.message || 'Error al iniciar sesi√≥n')
    }
  }

  const handleFormSubmit = async (e) => {
    e.preventDefault()
    await handleSubmit(onSubmit)
  }

  const handleInputChange = (e) => {
    const { name, value } = e.target
    handleChange(name, value)
  }

  const handleInputBlur = (e) => {
    const { name } = e.target
    handleBlur(name)
  }

  return (
    <div className="login-container">
      <div className="login-card">
        <div className="login-header">
          <h1 className="login-title">Iniciar Sesi√≥n</h1>
          <p className="login-subtitle">Accede a tu cuenta para continuar</p>
        </div>

        <form
          className="login-form"
          onSubmit={handleFormSubmit}
          noValidate
        >
          {(error || errors.general) && (
            <div className="error-alert" role="alert">
              <span aria-hidden="true">‚ö†Ô∏è</span>
              {error || errors.general}
            </div>
          )}

          <div className="form-group">
            <label htmlFor="username" className="form-label">
              Usuario
              <span aria-hidden="true"> *</span>
            </label>
            <input
              id="username"
              name="username"
              type="text"
              value={values.username}
              onChange={handleInputChange}
              onBlur={handleInputBlur}
              className={`form-input ${touched.username && errors.username ? 'error' : ''}`}
              placeholder="Ingresa tu usuario"
              autoComplete="username"
              required
              aria-invalid={touched.username && errors.username ? 'true' : 'false'}
              aria-describedby={touched.username && errors.username ? 'username-error' : undefined}
            />
            {touched.username && errors.username && (
              <div id="username-error" className="form-error" role="alert">
                <span aria-hidden="true">‚ö†Ô∏è</span>
                {errors.username}
              </div>
            )}
          </div>

          <div className="form-group">
            <label htmlFor="password" className="form-label">
              Contrase√±a
              <span aria-hidden="true"> *</span>
            </label>
            <input
              id="password"
              name="password"
              type="password"
              value={values.password}
              onChange={handleInputChange}
              onBlur={handleInputBlur}
              className={`form-input ${touched.password && errors.password ? 'error' : ''}`}
              placeholder="Ingresa tu contrase√±a"
              autoComplete="current-password"
              required
              aria-invalid={touched.password && errors.password ? 'true' : 'false'}
              aria-describedby={touched.password && errors.password ? 'password-error' : undefined}
            />
            {touched.password && errors.password && (
              <div id="password-error" className="form-error" role="alert">
                <span aria-hidden="true">‚ö†Ô∏è</span>
                {errors.password}
              </div>
            )}
          </div>

          <button
            type="submit"
            className="login-button"
            disabled={isSubmitting}
            aria-describedby="login-button-description"
          >
            {isSubmitting ? (
              <>
                <div className="spinner" aria-hidden="true"></div>
                Iniciando sesi√≥n...
              </>
            ) : (
              'Iniciar Sesi√≥n'
            )}
          </button>
        </form>
      </div>
    </div>
  )
}

export default Login