import { createContext, useContext, useReducer, useEffect, useCallback, useMemo } from 'react'
import { logger } from '../utils/logger'

const AuthContext = createContext()

const authReducer = (state, action) => {
  switch (action.type) {
    case 'LOGIN_START':
      return {
        ...state,
        isLoading: true,
        error: null
      }
    case 'LOGIN_SUCCESS':
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        user: action.payload.user,
        error: null
      }
    case 'LOGIN_FAILURE':
      return {
        ...state,
        isAuthenticated: false,
        isLoading: false,
        user: null,
        error: action.payload.error
      }
    case 'LOGOUT':
      return {
        ...state,
        isAuthenticated: false,
        isLoading: false,
        user: null,
        error: null
      }
    case 'CLEAR_ERROR':
      return {
        ...state,
        error: null
      }
    case 'RESTORE_SESSION':
      return {
        ...state,
        isAuthenticated: action.payload.isAuthenticated,
        user: action.payload.user,
        isLoading: false
      }
    default:
      return state
  }
}

const initialState = {
  isAuthenticated: false,
  isLoading: true,
  user: null,
  error: null
}

let memoryToken = null

export const AuthProvider = ({ children }) => {
  const [state, dispatch] = useReducer(authReducer, initialState)

  const setAuthToken = useCallback(async (token) => {
    memoryToken = token
    // Import apiService dynamically to avoid circular dependency
    const { default: apiService } = await import('../services/apiService')
    apiService.setAuthToken(token)
    logger.info('Token stored securely in memory')
  }, [])

  const getAuthToken = useCallback(() => {
    return memoryToken
  }, [])

  const clearAuthToken = useCallback(async () => {
    memoryToken = null
    // Import apiService dynamically to avoid circular dependency
    const { default: apiService } = await import('../services/apiService')
    apiService.setAuthToken(null)
    logger.info('Token cleared from memory')
  }, [])

  const login = useCallback(async (userData) => {
    try {
      dispatch({ type: 'LOGIN_START' })

      dispatch({
        type: 'LOGIN_SUCCESS',
        payload: { user: userData }
      })

      logger.info('User logged in successfully')
    } catch (error) {
      logger.error('Login failed', error)
      dispatch({
        type: 'LOGIN_FAILURE',
        payload: { error: error.message }
      })
    }
  }, [])

  const logout = useCallback(async () => {
    await clearAuthToken()
    sessionStorage.removeItem('user_data')
    sessionStorage.removeItem('auth_token')
    dispatch({ type: 'LOGOUT' })
    logger.info('User logged out')
  }, [clearAuthToken])

  const clearError = useCallback(() => {
    dispatch({ type: 'CLEAR_ERROR' })
  }, [])

  useEffect(() => {
    const initializeAuth = async () => {
      logger.info('Initializing authentication...')
      const storedUser = sessionStorage.getItem('user_data')
      const storedToken = sessionStorage.getItem('auth_token')

      logger.info('Stored data check:', {
        hasUser: !!storedUser,
        hasToken: !!storedToken
      })

      if (storedUser && storedToken) {
        try {
          const userData = JSON.parse(storedUser)
          memoryToken = storedToken

          // Import apiService dynamically and set token
          const { default: apiService } = await import('../services/apiService')
          apiService.setAuthToken(storedToken)

          dispatch({
            type: 'RESTORE_SESSION',
            payload: { isAuthenticated: true, user: userData }
          })
          logger.info('Session restored from storage')
        } catch (error) {
          logger.error('Failed to restore session', error)
          sessionStorage.removeItem('user_data')
          sessionStorage.removeItem('auth_token')
          dispatch({
            type: 'RESTORE_SESSION',
            payload: { isAuthenticated: false, user: null }
          })
        }
      } else {
        logger.info('No stored session found, starting fresh')
        dispatch({
          type: 'RESTORE_SESSION',
          payload: { isAuthenticated: false, user: null }
        })
      }
    }

    initializeAuth()
  }, [])

  const value = useMemo(() => ({
    ...state,
    login,
    logout,
    clearError,
    setAuthToken,
    getAuthToken,
    clearAuthToken
  }), [state, login, logout, clearError, setAuthToken, getAuthToken, clearAuthToken])

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  )
}

export const useAuth = () => {
  const context = useContext(AuthContext)
  if (!context) {
    throw new Error('useAuth must be used within AuthProvider')
  }
  return context
}