import { logger } from '../utils/logger'

const API_ENDPOINTS = {
  LOGIN: import.meta.env.VITE_API_CALL,
  LIST: import.meta.env.VITE_API_GET_USER,
  ADD: import.meta.env.VITE_API_ADD,
  REMOVE: import.meta.env.VITE_API_REMOVE
}

// Debug: Log API endpoints configuration
logger.info('API Endpoints configured:', API_ENDPOINTS)

// Validate required environment variables
const requiredEndpoints = ['LOGIN', 'LIST', 'ADD', 'REMOVE']
const missingEndpoints = requiredEndpoints.filter(key => !API_ENDPOINTS[key])

if (missingEndpoints.length > 0) {
  logger.error('Missing required API endpoints:', missingEndpoints)
  logger.error('Please check your environment variables:', {
    VITE_API_CALL: import.meta.env.VITE_API_CALL,
    VITE_API_ADD: import.meta.env.VITE_API_ADD,
    VITE_API_REMOVE: import.meta.env.VITE_API_REMOVE,
    VITE_API_GET_USER: import.meta.env.VITE_API_GET_USER
  })
}

class ApiService {
  constructor() {
    this.authToken = null
  }

  setAuthToken(token) {
    this.authToken = token
    logger.info('API Service: Token updated', { hasToken: !!token })
  }

  getAuthHeaders() {
    const headers = {
      'Content-Type': 'application/json',
      'Accept': 'application/json'
    }

    // Only add Origin header in development
    if (import.meta.env.MODE === 'development') {
      headers['Origin'] = 'http://localhost:3001'
    }

    if (this.authToken) {
      headers['Authorization'] = `Bearer ${this.authToken}`
    }

    return headers
  }

  async makeRequest(url, options = {}) {
    try {
      const defaultHeaders = this.getAuthHeaders()

      const config = {
        ...options,
        headers: {
          ...defaultHeaders,
          ...options.headers
        }
      }

      logger.info(`Making API request to: ${url}`)
      logger.debug('Request config:', config)

      const response = await fetch(url, config)

      logger.info(`Response status: ${response.status} ${response.statusText}`)

      if (!response.ok) {
        const errorText = await response.text()
        logger.error(`HTTP error ${response.status}:`, errorText)
        throw new Error(`HTTP ${response.status}: ${errorText}`)
      }

      const contentType = response.headers.get('content-type')
      if (contentType && contentType.includes('application/json')) {
        const data = await response.json()
        logger.debug('API response received', data)
        return data
      } else {
        const text = await response.text()
        logger.debug('API text response received', text)
        return { message: text }
      }
    } catch (error) {
      logger.error('API request failed', error)

      // Handle CORS errors specifically
      if (error.message.includes('NetworkError') || error.message.includes('CORS')) {
        throw new Error('Error de CORS: El servidor no permite peticiones desde este dominio. Contacte al administrador del API para configurar CORS.')
      }

      throw new Error(error.message || 'Error en la comunicaci√≥n con el servidor')
    }
  }

  async login(credentials) {
    const payload = {
      username: credentials.username,
      password: credentials.password
    }

    return this.makeRequest(API_ENDPOINTS.LOGIN, {
      method: 'POST',
      body: JSON.stringify(payload)
    })
  }

  async getUsers() {
    const payload = {
      data: "getusers"
    }

    try {
      return await this.makeRequest(API_ENDPOINTS.LIST, {
        method: 'POST',
        body: JSON.stringify(payload)
      })
    } catch (error) {
      // If CORS error in production, provide helpful error message
      if (error.message.includes('CORS')) {
        logger.warn('CORS error detected, API needs to allow GitHub Pages domain')
        throw new Error('Error de CORS: El API debe permitir peticiones desde GitHub Pages. Dominio requerido: https://netproadmin.store')
      }
      throw error
    }
  }

  async createUser(userData) {
    const payload = {
      data: userData
    }

    return this.makeRequest(API_ENDPOINTS.ADD, {
      method: 'POST',
      body: JSON.stringify(payload)
    })
  }

  async deleteUser(userId) {
    const payload = {
      data: {
        id: userId
      }
    }

    return this.makeRequest(API_ENDPOINTS.REMOVE, {
      method: 'POST',
      body: JSON.stringify(payload)
    })
  }
}

const apiService = new ApiService()

export default apiService