import { useState, useCallback } from 'react'
import { validateForm, validateField } from '../utils/validation'

export const useForm = (initialValues, validationSchema) => {
  const [values, setValues] = useState(initialValues)
  const [errors, setErrors] = useState({})
  const [touched, setTouched] = useState({})
  const [isSubmitting, setIsSubmitting] = useState(false)

  const handleChange = useCallback((name, value) => {
    setValues(prev => ({
      ...prev,
      [name]: value
    }))

    if (touched[name] && validationSchema && validationSchema[name]) {
      const fieldError = validateField(value, validationSchema[name])
      setErrors(prev => ({
        ...prev,
        [name]: fieldError
      }))
    }
  }, [touched, validationSchema])

  const handleBlur = useCallback((name) => {
    setTouched(prev => ({
      ...prev,
      [name]: true
    }))

    if (validationSchema && validationSchema[name]) {
      const fieldError = validateField(values[name], validationSchema[name])
      setErrors(prev => ({
        ...prev,
        [name]: fieldError
      }))
    }
  }, [values, validationSchema])

  const validate = useCallback(() => {
    if (!validationSchema) return { isValid: true, errors: {} }

    const validation = validateForm(values, validationSchema)
    setErrors(validation.errors)
    return validation
  }, [values, validationSchema])

  const handleSubmit = useCallback(async (onSubmit) => {
    setIsSubmitting(true)
    console.log("üîÑ useForm handleSubmit called")
    console.log("üìù Form values:", values)

    const validation = validate()
    console.log("‚úÖ Validation result:", validation)

    if (validation.isValid) {
      try {
        console.log("üöÄ Calling onSubmit with values:", values)
        await onSubmit(values)
      } catch (error) {
        console.error('Form submission error:', error)
        // Re-throw error so component can handle it
        throw error
      }
    }
     else {
      console.log("‚ùå Form validation failed:", validation.errors)
    }

    setIsSubmitting(false)
    return validation.isValid
  }, [values, validate])

  const reset = useCallback(() => {
    setValues(initialValues)
    setErrors({})
    setTouched({})
    setIsSubmitting(false)
  }, [initialValues])

  const setFieldError = useCallback((name, error) => {
    setErrors(prev => ({
      ...prev,
      [name]: error
    }))
  }, [])

  return {
    values,
    errors,
    touched,
    isSubmitting,
    handleChange,
    handleBlur,
    handleSubmit,
    validate,
    reset,
    setFieldError
  }
}