import { useState, useEffect } from 'react'
import { useAuth } from '../context/AuthContext'
import apiService from '../services/apiService'
import { logger } from '../utils/logger'

const Debug = () => {
  const [debugInfo, setDebugInfo] = useState({})
  const [testResults, setTestResults] = useState({})
  const { isAuthenticated, user } = useAuth()

  useEffect(() => {
    const gatherDebugInfo = () => {
      const info = {
        // Environment
        mode: import.meta.env.MODE,
        baseUrl: import.meta.env.BASE_URL,

        // API Configuration
        apiEndpoints: {
          VITE_API_CALL: import.meta.env.VITE_API_CALL,
          VITE_API_ADD: import.meta.env.VITE_API_ADD,
          VITE_API_REMOVE: import.meta.env.VITE_API_REMOVE
        },

        // Current URL
        currentUrl: window.location.href,
        origin: window.location.origin,
        pathname: window.location.pathname,

        // Auth state
        isAuthenticated,
        hasUser: !!user,

        // Storage
        sessionStorage: {
          hasUserData: !!sessionStorage.getItem('user_data'),
          hasAuthToken: !!sessionStorage.getItem('auth_token')
        },

        // Browser info
        userAgent: navigator.userAgent,
        language: navigator.language
      }

      setDebugInfo(info)
      logger.info('Debug info gathered:', info)
    }

    gatherDebugInfo()
  }, [isAuthenticated, user])

  const testApiEndpoint = async (name, url) => {
    try {
      logger.info(`Testing ${name} endpoint: ${url}`)

      const response = await fetch(url, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      })

      const result = {
        status: response.status,
        statusText: response.statusText,
        ok: response.ok,
        headers: Object.fromEntries(response.headers.entries())
      }

      if (response.ok) {
        try {
          const data = await response.text()
          result.data = data
        } catch (e) {
          result.dataError = e.message
        }
      } else {
        try {
          const errorText = await response.text()
          result.error = errorText
        } catch (e) {
          result.error = 'Could not read error response'
        }
      }

      return result
    } catch (error) {
      return {
        error: error.message,
        type: 'network_error'
      }
    }
  }

  const runApiTests = async () => {
    logger.info('Running API tests...')
    setTestResults({ testing: true })

    const tests = {}

    // Test each endpoint
    if (debugInfo.apiEndpoints?.VITE_API_CALL) {
      tests.login = await testApiEndpoint('LOGIN', debugInfo.apiEndpoints.VITE_API_CALL)
    }

    if (debugInfo.apiEndpoints?.VITE_API_ADD) {
      tests.add = await testApiEndpoint('ADD', debugInfo.apiEndpoints.VITE_API_ADD)
    }

    if (debugInfo.apiEndpoints?.VITE_API_REMOVE) {
      tests.remove = await testApiEndpoint('REMOVE', debugInfo.apiEndpoints.VITE_API_REMOVE)
    }

    setTestResults(tests)
    logger.info('API tests completed:', tests)
  }

  return (
    <div style={{ padding: '20px', fontFamily: 'monospace', backgroundColor: '#f5f5f5', minHeight: '100vh' }}>
      <h1>üêõ Debug Information</h1>

      <div style={{ marginBottom: '20px' }}>
        <button
          onClick={runApiTests}
          style={{
            padding: '10px 20px',
            backgroundColor: '#007bff',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
          üß™ Test API Endpoints
        </button>
      </div>

      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px' }}>
        <div>
          <h2>üìä Environment Info</h2>
          <pre style={{ backgroundColor: 'white', padding: '15px', borderRadius: '4px', overflow: 'auto' }}>
            {JSON.stringify(debugInfo, null, 2)}
          </pre>
        </div>

        <div>
          <h2>üß™ API Test Results</h2>
          <pre style={{ backgroundColor: 'white', padding: '15px', borderRadius: '4px', overflow: 'auto' }}>
            {JSON.stringify(testResults, null, 2)}
          </pre>
        </div>
      </div>

      <div style={{ marginTop: '20px' }}>
        <h2>üîç Console Logs</h2>
        <p>Check the browser console for detailed logs. Current log level: INFO</p>
        <p>Open DevTools ‚Üí Console to see all debug information</p>
      </div>

      <div style={{ marginTop: '20px' }}>
        <h2>üìù Quick Checklist</h2>
        <ul>
          <li>‚úÖ Environment variables loaded: {debugInfo.apiEndpoints?.VITE_API_CALL ? 'Yes' : 'No'}</li>
          <li>‚úÖ Base URL configured: {debugInfo.baseUrl || 'Default'}</li>
          <li>‚úÖ Current mode: {debugInfo.mode || 'Unknown'}</li>
          <li>‚úÖ Authentication state: {isAuthenticated ? 'Authenticated' : 'Not authenticated'}</li>
        </ul>
      </div>

      <div style={{ marginTop: '20px', padding: '15px', backgroundColor: '#fff3cd', borderRadius: '4px' }}>
        <h3>üö® Common Issues</h3>
        <ul>
          <li><strong>CORS errors:</strong> Check if API allows requests from your domain</li>
          <li><strong>404 errors:</strong> Verify API endpoints are correct</li>
          <li><strong>CSS not loading:</strong> Check if assets are being served from correct path</li>
          <li><strong>Environment variables:</strong> Ensure they start with VITE_ and are configured</li>
        </ul>
      </div>
    </div>
  )
}

export default Debug