import { useState, lazy, Suspense } from 'react'
import { useAuth } from '../context/AuthContext'
import LoadingSpinner from '../components/LoadingSpinner'
import '../styles/Panel.css'

const Listado = lazy(() => import('../components/Listado'))
const Agregar = lazy(() => import('../components/Agregar'))
const Eliminar = lazy(() => import('../components/Eliminar'))

const TABS = {
  LISTADO: 'listado',
  AGREGAR: 'agregar',
  ELIMINAR: 'eliminar'
}

const Panel = () => {
  const { user, logout } = useAuth()
  const [activeTab, setActiveTab] = useState(TABS.LISTADO)

  const handleLogout = async () => {
    if (window.confirm('¬øEst√°s seguro de que quieres cerrar sesi√≥n?')) {
      await logout()
    }
  }

  const handleTabChange = (tab) => {
    setActiveTab(tab)
  }

  const renderTabContent = () => {
    switch (activeTab) {
      case TABS.LISTADO:
        return <Listado />
      case TABS.AGREGAR:
        return <Agregar />
      case TABS.ELIMINAR:
        return <Eliminar />
      default:
        return <Listado />
    }
  }

  return (
    <div className="panel-container">
      <header className="panel-header">
        <h1 className="panel-title">Panel de Administraci√≥n</h1>
        <div className="user-info">
          <span className="user-greeting">
            Bienvenido, {user?.nombre} {user?.apellido}
          </span>
          <button
            onClick={handleLogout}
            className="logout-button"
            type="button"
            aria-label="Cerrar sesi√≥n"
          >
            Cerrar Sesi√≥n
          </button>
        </div>
      </header>

      <main className="panel-content">
        <nav className="tab-navigation" role="tablist" aria-label="Navegaci√≥n del panel">
          <ul className="tab-list">
            <li className="tab-item" role="none">
              <button
                id="tab-listado"
                className={`tab-button ${activeTab === TABS.LISTADO ? 'active' : ''}`}
                onClick={() => handleTabChange(TABS.LISTADO)}
                role="tab"
                aria-selected={activeTab === TABS.LISTADO}
                aria-controls="tabpanel-listado"
                type="button"
              >
                <span className="tab-icon" aria-hidden="true">üìã</span>
                Listado
              </button>
            </li>
            <li className="tab-item" role="none">
              <button
                id="tab-agregar"
                className={`tab-button ${activeTab === TABS.AGREGAR ? 'active' : ''}`}
                onClick={() => handleTabChange(TABS.AGREGAR)}
                role="tab"
                aria-selected={activeTab === TABS.AGREGAR}
                aria-controls="tabpanel-agregar"
                type="button"
              >
                <span className="tab-icon" aria-hidden="true">‚ûï</span>
                Agregar
              </button>
            </li>
            <li className="tab-item" role="none">
              <button
                id="tab-eliminar"
                className={`tab-button ${activeTab === TABS.ELIMINAR ? 'active' : ''}`}
                onClick={() => handleTabChange(TABS.ELIMINAR)}
                role="tab"
                aria-selected={activeTab === TABS.ELIMINAR}
                aria-controls="tabpanel-eliminar"
                type="button"
              >
                <span className="tab-icon" aria-hidden="true">üóëÔ∏è</span>
                Eliminar
              </button>
            </li>
          </ul>
        </nav>

        <div
          className="tab-content"
          role="tabpanel"
          aria-labelledby={`tab-${activeTab}`}
          id={`tabpanel-${activeTab}`}
        >
          <Suspense fallback={<LoadingSpinner message="Cargando contenido..." />}>
            {renderTabContent()}
          </Suspense>
        </div>
      </main>
    </div>
  )
}

export default Panel